    },
    route:{
		data(transition){
            this.compTitle = transition.to.name
		}
	},
    components: {
        tabBarWithRouter,
        annotate,
        preCode,
    },
    methods: {
    }
}
</script>

<style media="screen">
.content{
    margin-top: 2%;
    height: 3em;
    line-height: 3;
    width: 100%;
    display: inline-block;
